import Link from "next/link"
import { BookOpen, Search, BarChart3, Library, Shield } from "lucide-react"
import { Button } from "@/components/ui/button"
import { createClient } from "@/lib/supabase/server"
import { SignOutButton } from "@/components/signout-button"

export async function Header() {
  const supabase = await createClient()
  const {
    data: { user },
  } = await supabase.auth.getUser()

  let isAdmin = false
  if (user) {
    const { data: profile } = await supabase.from("user_profiles").select("is_admin").eq("id", user.id).maybeSingle()
    isAdmin = profile?.is_admin || false
  }

  return (
    <header className="sticky top-0 z-50 w-full border-b border-border bg-card/95 backdrop-blur supports-[backdrop-filter]:bg-card/60">
      <div className="container mx-auto flex h-16 items-center justify-between px-4">
        <Link href="/" className="flex items-center gap-2">
          <Library className="h-6 w-6 text-primary" />
          <span className="text-xl font-semibold text-foreground">LibraryHub</span>
        </Link>

        <nav className="hidden md:flex items-center gap-6">
          <Link
            href="/books"
            className="flex items-center gap-2 text-sm font-medium text-muted-foreground hover:text-foreground transition-colors"
          >
            <Search className="h-4 w-4" />
            Browse Books
          </Link>
          <Link
            href="/analytics"
            className="flex items-center gap-2 text-sm font-medium text-muted-foreground hover:text-foreground transition-colors"
          >
            <BarChart3 className="h-4 w-4" />
            Analytics
          </Link>
          {user && (
            <Link
              href="/my-books"
              className="flex items-center gap-2 text-sm font-medium text-muted-foreground hover:text-foreground transition-colors"
            >
              <BookOpen className="h-4 w-4" />
              My Books
            </Link>
          )}
          {isAdmin && (
            <Link
              href="/admin"
              className="flex items-center gap-2 text-sm font-medium text-primary hover:text-primary/80 transition-colors"
            >
              <Shield className="h-4 w-4" />
              Admin
            </Link>
          )}
            <Link
                href="/admin"
                className="flex items-center gap-2 text-sm font-medium text-primary hover:text-primary/80 transition-colors"
            >
                <Shield className="h-4 w-4" />
                Admin
            </Link>
        </nav>

        <div className="flex items-center gap-3">
          {user ? (
            <SignOutButton />
          ) : (
            <>
              <Button variant="ghost" size="sm" asChild>
                <Link href="/auth/login">Sign In</Link>
              </Button>
              <Button size="sm" asChild>
                <Link href="/auth/sign-up">Get Started</Link>
              </Button>
            </>
          )}
        </div>
      </div>
    </header>
  )
}
